import cTitle from 'components/title';
import { Navbar } from 'mint-ui';
export default {
    data() {
        return {
            _id: "",
            commission:{},
            order:{},
            goods:{},
        };
    },


    activated() {
        this._id=this.$route.params.id;
        //获取数据
        this.getData();
    },
    methods: {
        //获取数据
        getData() {
            let that = this;
            let json = { id: this._id };
            $http.get('finance.income.get-detail', json, "加载中...").then(function (response) {
                console.log(response)
                if (response.result == 1) {
                    that.commission=response.data.commission;
                    that.order=response.data.order;
                    that.goods=response.data.goods;
                } else {
                    console.log(response.msg);
                }

            }, function (response) {
                console.log(response);
            });
        }

    },
    components: { cTitle }
}


